.select:hover::after {
  transform: scale(1.5);
}
*/

.search {
  position: absolute;
  top: 242px;
  left: 25%;
  font-size: 2px;
  /* width: 100vw; */
}

.search > button {
  width: 505px;
  position: absolute;
  left: 0;
  top: 353px;
  border: none;
  text-transform: inherit !important;
  background-color: #ff7779;
  color: white;
}

.search > button:hover {
  background-color: white;
  color: #ff7779;
}

.rdrDefinedRangesWrapper {
  width: 150px;
}

.rdrInputRangeInput {
  width: 60px;
  font-size: 1rem;
}

.rdrDateDisplayItem input {
  font-size: 1rem;
}

.rdrMonthAndYearWrapper {
  height: 40px;
}

button.rdrNextPrevButton {
margin: 0 5px;
}

.rdrMonthAndYearPickers select {
  width: 10em;
}

.todo .orange {
  background-color: orange;
  transition: background-color 1s ease;

}

.todo .red {
  background-color: red;
  transition: background-color 1s ease;

}

.button_container{
  display: flex;
  justify-content: center;
  text-align: center;
  font-weight: 700px;
  flex-direction: column;

}

.button_container button{
  cursor: pointer;
  margin: 5px 8px;
  border-radius: 30px;
  text-transform: inherit;
  font-weight: 700px;

}

.timer-wrapper {
  display: flex;
}